export default {
    validEmail: (v) => /.+@.+\..+/.test(v) || "     ",
    NameRequired:(v) => !!v || "    ",
    nameLength:(v) => (v && v.length <= 10) || "        ",
    minNameLen: (len) => (v) =>
      (v || "").length >= len ||
      `         ${len} `,
    length: (len) => (v) =>
      (v || "").length >= len ||
      `     ${len} `,
    password: (v) =>
      !!(v || "").match(
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[^\w])).+$/
      ) ||
      "             !",
      
    required: (v) => !!v || "",
    confirmPassword: (value) =>
      value === this.newUser.password ||
      "   ",
  }